
.big {
    width: 100vw;
    height: 300vh;
    background-color: #333333;
    .small {
        width: 100vw;
        height: 100vw;
        background-color: #444444;
    }
}
.glass {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background-color: #cccccc22;
}

.all {
    width: 100vw;
    background-color: #333333;
    position: fixed;
    top: 0;
    left: 0;
    .top {
        background-color: #444444;
        width: 100vw;
        height: 20vw;
    }
    .middle {
        background-color: #555555;
        width: 100vw;
        height: calc(100% - 40vw);
        overflow-y: scroll;
    }
    .bottom {
        background-color: #444444;
        width: 100vw;
        height: 20vw;
    }
}
